#box {
    padding: 1vh;
    height: 50vh;
    font-family: Optima;
}

#proj-img {
    height:17vh;
    width:35vh;
    padding: 1vh;
    border-style:inset;
    border-width:1px;
    border-radius: 10%;
    border-color: rgb(247, 252, 251);
}

#proj-img:hover {
    border-width:3px;
    border-color: rgb(216, 212, 212);
}

#description {
    color:rgb(63, 63, 65);
    width: 35vh;
    margin: auto;
}

#tools {
    font-style: italic;
    font-size: smaller;
}
