
#project-background {
    height: 140vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(247, 248, 240);
}

#project-box {
    width: 160vh;
    height: 120vh;
    padding: 3vh;
    background-color: rgb(252, 253, 255);
    border-radius: 5%;
    border-style: double; 
    border-width: 1vh;
    border-color: rgb(39, 8, 48);
}

#name {
    padding-bottom: 1vh;
}